@import './var.scss';
.community {
  padding-bottom: 37px;
  // min-height: calc(100vh - 100px);
  // background: #1e1e2f;

  .community-layout {
    margin: 0 auto;
    width: 1180px;
  }
  .community-banner {
    height: 340px;
    background: url('../images/banner_documents.png') no-repeat center;
    background-size: cover;

    .banner-inner {
      .title {
        padding-top: 29px;
        font-size: 80px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
        line-height: 112px;
      }
      .desc {
        margin-top: 14px;
        font-size: 30px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #ffffff;
        line-height: 42px;
      }
      .view-btn {
        display: inline-block;
        margin-top: 42px;
        padding: 6px 23px;
        font-size: 20px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #5a14e1;
        line-height: 28px;
        background: #ffffff;
        border-radius: 20px;
        cursor: pointer;
        transition: all 0.3s;
        &:hover {
          background-color: #5a14e1;
          color: #fff;
        }
      }
    }
  }
  .community-main {
    padding: 50px 0;
    .community-flex {
      display: flex;
    }

    .quick-start {
      flex: 1;
    }
    .community-title {
      margin-bottom: 20px;
      font-size: 30px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #ffffff;
      line-height: 42px;
    }
    .quick-wrap {
      display: flex;
      .quick-row {
        padding: 30px;
        flex: 1;
        margin-right: 30px;
        background: #2a2a3f url('../images/bg_document1.png') no-repeat;
        background-position: right top;
        background-size: 130px 130px;
        border-radius: 10px;
        &:last-child {
          margin-right: 0;
          background-image: url('../images/bg_document2.png');
        }
      }
    }

    .community-aside {
      margin-left: 60px;
      width: 250px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .link-way {
        display: block;
        padding: 10px 20px;
        font-size: 15px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
        line-height: 21px;
        border-radius: 5px;
        background: #2a2a3f url('../images/ico_document_link.svg') no-repeat right 18px center;
      }
    }

    .row-title {
      font-size: 20px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #ffffff;
      line-height: 28px;
    }
    .row-desc {
      margin-top: 26px;
      font-size: 20px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: rgba(255, 255, 255, 0.5);
      line-height: 30px;
    }
  }
  .mobile-header {
    margin-bottom: 18px;
  }
}

@include mobile-only {
  .community {
    .community-layout {
      width: 100%;
    }

    .community-banner {
      height: 140px;

      .banner-inner {
        padding: 0 35px;
        .title {
          padding-top: 12px;
          font-size: 30px;
          line-height: 42px;
        }
        .desc {
          margin-top: 5px;
          font-size: 15px;
          line-height: 21px;
        }
        .view-btn {
          margin-top: 16px;
          padding: 3px 16px;
          font-size: 13px;
          line-height: 18px;
        }
      }
    }

    .community-main {
      padding: 0 15px;
      .community-flex {
        display: block;
      }

      .quick-start {
        flex: 1;
      }
      .community-title {
        padding-top: 24px;
        margin-bottom: 12px;
        font-size: 20px;
        line-height: 28px;
      }
      .quick-wrap {
        display: block;
        .quick-row {
          display: block;
          padding: 16px 20px 20px;
          margin-right: 0;
          margin-bottom: 10px;
          background-size: 110px 110px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      .community-aside {
        margin-left: 0px;
        width: 100%;
        display: block;
        .link-way {
          display: block;
        }
      }

      .row-title {
        font-size: 15px;
        line-height: 21px;
      }
      .row-desc {
        margin-top: 10px;
        font-size: 15px;
        line-height: 26px;
      }
    }
  }
}

@include small-desktop-only {
  .community {
    .community-layout {
      max-width: 75em;
      margin: 0 auto;
    }
  }
}
